import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  Row,
  Col,
  Divider,
  DatePicker
  // Tooltip
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/fixedAssetTransferReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle,
  StatusBadge,
  TransNo
} from "../../../App/app.style";

import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
// import { getTransactions } from "../../../../redux/purchase/purchaseOrder/saga";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";

class FixedAssetTransferReport extends Component {
  state = {
    filter: {
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      id_from_location: null,
      id_to_location: null,
      id_asset_category: null,
      id_asset_type: null,
      id_department: null,
      status: null,
      sort_by: "transfer_date",
      sort_type: "asc"
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getFixedAssetTransferReports } = this.props;
    getFixedAssetTransferReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.fixedAssetTransferReport.error) {
      notification("error", this.props.fixedAssetTransferReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.fixedAssetTransferReport.success) {
      notification("success", this.props.fixedAssetTransferReport.success);
      this.props.clearSuccess();
    }
  }

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else if (name === "id_asset_category") {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value,
          id_asset_type: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_from_location) {
      string += "id_from_location=" + filter.id_from_location;
    }
    if (filter.id_to_location) {
      string
        ? (string += "&id_to_location=" + filter.id_to_location)
        : (string += "id_to_location=" + filter.id_to_location);
    }
    if (filter.id_asset_category) {
      string
        ? (string += "&id_asset_category=" + filter.id_asset_category)
        : (string += "id_asset_category=" + filter.id_asset_category);
    }
    if (filter.id_asset_type) {
      string
        ? (string += "&id_asset_type=" + filter.id_asset_type)
        : (string += "id_asset_type=" + filter.id_asset_type);
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.transaction_type) {
      string
        ? (string += "&transaction_type=" + filter.transaction_type)
        : (string += "transaction_type=" + filter.transaction_type);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    // if (filter.due_date_start && filter.due_date_end) {
    //   string
    //     ? (string +=
    //         "&due_date_start=" + filter.due_date_start + "&due_date_end=" + filter.due_date_end)
    //     : (string +=
    //         "due_date_start=" + filter.due_date_start + "&due_date_end=" + filter.due_date_end);
    // }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getFixedAssetTransferReports } = this.props;
    //const { current_page } = this.props.fixedAssetTransferReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getFixedAssetTransferReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getFixedAssetTransferReports } = this.props;
    this.setState({
      filter: {
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        id_from_location: null,
        id_to_location: null,
        id_asset_category: null,
        id_asset_type: null,
        id_department: null,
        status: null,
        sort_by: "transfer_date",
        sort_type: "asc"
      }
    });
    getFixedAssetTransferReports();
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "transferDate" }),
        dataIndex: "transfer_date",
        rowKey: "transfer_date",
        date: true,
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "jobCostings.add.code" }),
        dataIndex: "fixed_asset_code",
        rowKey: "fixed_asset_code",
        width: "15%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "ordered";
              break;
            // case 2:
            //   className = "closed";
            //   break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>{text}</TransNo>
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "jobCostings.add.name" }),
        dataIndex: "fixed_asset_name",
        rowKey: "fixed_asset_name"
      },
      {
        title: getLanguage({ id: "serialNo" }),
        dataIndex: "serial_no",
        rowKey: "serial_no"
      },
      {
        title: getLanguage({ id: "fromLocation" }),
        dataIndex: "from_location_name",
        rowKey: "from_location_name"
      },
      {
        title: getLanguage({ id: "toLocation" }),
        dataIndex: "to_location_name",
        rowKey: "to_location_name"
      },
      {
        title: getLanguage({ id: "qtyTransferred" }),
        dataIndex: "qty",
        rowKey: "qty",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by"
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status_string",
        rowKey: "status_string",
        align: "center",
        render: (text, row) => {
          let className;
          let display;
          switch (row.status) {
            case 1:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "processed" });
              break;
            // case 2:
            //   className = "closed";
            //   display = "Closed";
            //   break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];
    // Data setup
    const {
      fixed_asset_transfer_reports,
      locations,
      // departments,
      asset_types,
      asset_categories,
      loading,
      loadingForeign,
      filter,
      filter_select
    } = this.props.fixedAssetTransferReport
      ? this.props.fixedAssetTransferReport
      : [];
    //console.log(filter);

    let dataSource = [];
    fixed_asset_transfer_reports.map(detail => {
      let status_string;

      switch (detail.status) {
        case 1:
          status_string = getLanguage({ id: "new" });
          break;
        case 2:
          status_string = getLanguage({ id: "processed" });
          break;
        case 3:
          status_string = getLanguage({ id: "cancelled" });
          break;
        default:
          status_string = "-";
          break;
      }

      dataSource.push({
        ...detail,
        status_string
      });
    });

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const fileName = getLanguage({ id:"fixedAssetTransferReports.pageHeader" })
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="fixedAssetTransferReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="fixedAssetTransferReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              margin=".5cm"
              scale={0.42}
              title="fixedAssetTransferReports.pageHeader"
            />

            <ExcelWrapper
              title="fixedAssetTransferReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="fromLocation" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_from_location"
                      )}
                      value={
                        this.state.filter.id_from_location
                          ? this.state.filter.id_from_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="toLocation" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_to_location"
                      )}
                      value={
                        this.state.filter.id_to_location
                          ? this.state.filter.id_to_location
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="transferDate" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col span={12}>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="assetCategories.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_asset_category"
                        )}
                        value={
                          this.state.filter.id_asset_category
                            ? this.state.filter.id_asset_category
                            : []
                        }
                        style={{ width: "95%" }}
                        placeholder={<IntlMessages id="select.assetCategory" />}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        //showArrow={false}
                      >
                        {asset_categories.map((data, key) => (
                          <Option key={key} value={data.id_asset_category}>
                            {`${data.asset_category_code} (${data.asset_category_name})`}
                          </Option>
                        ))}
                      </Select>
                    </Col>
                    <Col span={12}>
                      <label>
                        <IntlMessages id="assetTypes.pageHeader" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "id_asset_type"
                        )}
                        value={
                          this.state.filter.id_asset_type
                            ? this.state.filter.id_asset_type
                            : []
                        }
                        style={{ width: "100%" }}
                        placeholder={
                          <IntlMessages id="select.assetCategoryFirst" />
                        }
                        optionFilterProp="children"
                        filterOption={true}
                        loading={loadingForeign}
                        allowClear={true}
                        //showArrow={false}
                      >
                        {asset_types.map((data, key) =>
                          this.state.filter.id_asset_category ===
                          data.id_asset_category ? (
                            <Option key={key} value={data.id_asset_type}>
                              {`${data.asset_type_code} (${data.asset_type_name})`}
                            </Option>
                          ) : null
                        )}
                      </Select>
                    </Col>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transfer_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="transfer_date">
                            <IntlMessages id="transferDate" />
                          </Option>
                          <Option key={2} value="fixed_asset_name">
                            <IntlMessages id="fixedAssetName" />
                          </Option>
                          <Option key={3} value="from_location_name">
                            <IntlMessages id="fromLocation" />
                          </Option>
                          <Option key={4} value="to_location_name">
                            <IntlMessages id="toLocation" />
                          </Option>
                          <Option key={4} value="created_by">
                            <IntlMessages id="createdBy" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "90%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                  <Col span={12}>
                    <Col span={8}>
                      <label>Status</label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(this, "status")}
                        value={
                          this.state.filter.status
                            ? this.state.filter.status
                            : []
                        }
                        style={{ width: "95%" }}
                        placeholder={getLanguage({ id: "status" })}
                        optionFilterProp="children"
                        filterOption={true}
                        // loading={this.props.location.loadingFilterSites}
                        allowClear={true}
                        //showArrow={false}
                      >
                        {/* <Option value={4}>New</Option> */}
                        <Option value={1}>{getLanguage({ id: "new" })}</Option>
                        <Option value={2}>
                          {getLanguage({ id: "processed" })}
                        </Option>
                        <Option value={3}>
                          {getLanguage({ id: "cancelled" })}
                        </Option>
                      </Select>
                    </Col>
                    <Col span={8} style={{ marginTop: "24px" }}>
                      <Button
                        size="small"
                        type="primary"
                        style={{ marginRight: "10px" }}
                        onClick={this.handleFilter}
                      >
                        <IntlMessages id="filter.only" />
                      </Button>
                      <Button onClick={this.handleResetFilter} size="small">
                        <IntlMessages id="filter.reset" />
                      </Button>
                    </Col>
                  </Col>
                </Row>
                {/* <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  
                </Row> */}
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clickFilter" />} />
            ) : dataSource.length === 0 &&
              !this.props.fixedAssetTransferReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="fixedAssetTransferReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    rowKey={record => record.id_fixed_asset_transfer}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    fixedAssetTransferReport: state.fixedAssetTransferReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  FixedAssetTransferReport
);
